@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;500;700&display=swap");
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Roboto", sans-serif;
}
@media (min-width: 0px) and (max-width: 539px) {
  body {
    padding: 48px 0;
  }
}
@media (min-width: 540px) and (max-width: 799px) {
  body {
    padding: 48px 20px;
  }
}
@media (min-width: 800px) and (max-width: 1129px) {
  body {
    padding: 72px calc(50vw - 400px);
  }
}
@media (min-width: 1130px) and (max-width: 1399px) {
  body {
    padding: 72px calc(50vw - 565px);
  }
}
@media (min-width: 1400px) and (max-width: 100000px) {
  body {
    padding: 96px calc(50vw - 700px);
  }
}

main {
  width: 100%;
  max-width: 1400px;
}
@media (min-width: 0px) and (max-width: 539px) {
  main {
    padding: 0 20px;
  }
}
@media (min-width: 540px) and (max-width: 799px) {
  main {
    padding: 0 20px;
  }
}
@media (min-width: 800px) and (max-width: 1129px) {
  main {
    padding: 0 56px;
  }
}
@media (min-width: 1130px) and (max-width: 1399px) {
  main {
    padding: 0 60px;
  }
}
@media (min-width: 1400px) and (max-width: 100000px) {
  main {
    padding: 0 195px;
  }
}

h3 {
  color: #121212;
  font-size: 42px;
  font-weight: 700;
  margin-block-start: 0;
  margin-block-end: 0;
}

p {
  color: #121212;
  font-size: 16px;
  font-weight: 300;
}

.bold {
  font-weight: 700;
}

button {
  padding: 13px 37px;
  border-radius: 40px;
  font-size: 16px;
  font-weight: 700;
  cursor: pointer;
}
button.filled {
  color: #fff;
  border: 3px solid #E4C1AE;
  background: #E4C1AE;
}
button.outline {
  color: #E4C1AE;
  border: 3px solid #E4C1AE;
  background: none;
}
button.invisible {
  margin: 0;
  padding: 0;
  border: none;
  background: none;
}

.image-section {
  display: flex;
}
@media (min-width: 0px) and (max-width: 539px) {
  .image-section {
    flex-direction: column;
  }
}
@media (min-width: 540px) and (max-width: 799px) {
  .image-section {
    flex-direction: column;
  }
}
@media (min-width: 800px) and (max-width: 1129px) {
  .image-section {
    flex-direction: row;
  }
}
@media (min-width: 1130px) and (max-width: 1399px) {
  .image-section {
    flex-direction: row;
  }
}
@media (min-width: 1400px) and (max-width: 100000px) {
  .image-section {
    flex-direction: row;
  }
}
@media (min-width: 1130px) and (max-width: 1399px) {
  .image-section {
    width: 1010px;
  }
}
@media (min-width: 1400px) and (max-width: 100000px) {
  .image-section {
    width: 1010px;
  }
}
.image-section * {
  flex: 1;
}
.image-section .img-container {
  position: relative;
}
@media (min-width: 0px) and (max-width: 539px) {
  .image-section .img-container {
    min-height: calc((100vw - 40px) / 16 * 9);
  }
}
@media (min-width: 540px) and (max-width: 799px) {
  .image-section .img-container {
    min-height: calc((100vw - 80px) / 21 * 9);
  }
}
@media (min-width: 800px) and (max-width: 1129px) {
  .image-section .img-container {
    min-height: 344px;
    min-width: 344px;
  }
}
@media (min-width: 1130px) and (max-width: 1399px) {
  .image-section .img-container {
    min-height: 330px;
    min-width: 505px;
  }
}
@media (min-width: 1400px) and (max-width: 100000px) {
  .image-section .img-container {
    min-height: 330px;
    min-width: 505px;
  }
}
.image-section .img-container img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.image-section .text-container {
  display: flex;
  align-items: center;
}
@media (min-width: 0px) and (max-width: 539px) {
  .image-section .text-container {
    padding: 20px 10px;
  }
}
@media (min-width: 540px) and (max-width: 799px) {
  .image-section .text-container {
    padding: 30px 20px;
  }
}
@media (min-width: 800px) and (max-width: 1129px) {
  .image-section .text-container {
    padding: 30px 30px 30px 60px;
  }
}
@media (min-width: 1130px) and (max-width: 1399px) {
  .image-section .text-container {
    padding: 30px 30px 30px 95px;
  }
}
@media (min-width: 1400px) and (max-width: 100000px) {
  .image-section .text-container {
    padding: 30px 30px 30px 95px;
  }
}
.image-section .text-container p {
  margin-top: 24px;
  margin-bottom: 32px;
}

#overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(18, 18, 18, 0.5);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
#overlay .popup {
  background: #fff;
  position: relative;
}
@media (min-width: 0px) and (max-width: 539px) {
  #overlay .popup {
    width: calc(100vw - 40px);
    padding: 40px 30px;
  }
}
@media (min-width: 540px) and (max-width: 799px) {
  #overlay .popup {
    width: calc(100vw - 80px);
    padding: 40px;
  }
}
@media (min-width: 800px) and (max-width: 1129px) {
  #overlay .popup {
    width: 650px;
    padding: 56px;
  }
}
@media (min-width: 1130px) and (max-width: 1399px) {
  #overlay .popup {
    width: 920px;
    padding: 56px 95px;
  }
}
@media (min-width: 1400px) and (max-width: 100000px) {
  #overlay .popup {
    width: 920px;
    padding: 56px 95px;
  }
}
#overlay .popup p {
  margin-top: 24px;
}
#overlay .popup #close-button {
  position: absolute;
  width: 20px;
  height: 20px;
}
@media (min-width: 0px) and (max-width: 539px) {
  #overlay .popup #close-button {
    top: 30px;
    right: 30px;
  }
}
@media (min-width: 540px) and (max-width: 799px) {
  #overlay .popup #close-button {
    top: 30px;
    right: 30px;
  }
}
@media (min-width: 800px) and (max-width: 1129px) {
  #overlay .popup #close-button {
    top: 46px;
    right: 46px;
  }
}
@media (min-width: 1130px) and (max-width: 1399px) {
  #overlay .popup #close-button {
    top: 46px;
    right: 46px;
  }
}
@media (min-width: 1400px) and (max-width: 100000px) {
  #overlay .popup #close-button {
    top: 46px;
    right: 46px;
  }
}
#overlay .popup #close-button img {
  width: 12px;
  height: 12px;
}
#overlay .popup #reset-button {
  margin-top: 20px;
}